*, :before, :after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  --gallery-gap: 10px;
  --column-number: 4;
}

.form-container {
  width: 100%;
  opacity: .8;
  background-color: #4e4bfd;
  padding-top: 15px;
  padding-bottom: 15px;
  position: fixed;
  top: 0;
  left: 0;
}

.search-form {
  margin-left: 50%;
  display: inline-block;
  position: relative;
  transform: translateX(-50%);
}

.search-form input {
  border: none;
  border-radius: 10px;
  outline: none;
  padding: 5px 10px;
  font-size: 20px;
}

.search-form button {
  border: 1px solid #a8a6a6;
  border-radius: 10px;
  outline: none;
  padding: 4px 10px;
  font-size: 20px;
  position: absolute;
  top: 0;
  right: 0;
}

.ico {
  display: block;
}

.gallery {
  gap: var(--gallery-gap);
  flex-wrap: wrap;
  margin: 72px 15px 25px;
  display: flex;
}

.photo-card {
  width: calc( (100% + var(--gallery-gap) ) / var(--column-number)  - var(--gallery-gap) );
  border: 1px solid #949191;
  flex-direction: column;
  gap: 5px;
  display: flex;
  box-shadow: 2px 2px 2px rgba(0, 0, 0, .5);
}

.photo-card img {
  width: 100%;
  min-height: 200px;
}

.info {
  text-align: center;
  flex-grow: 1;
  justify-content: space-around;
  align-items: center;
  font-size: 12px;
  display: flex;
}

.info-item {
  flex-direction: column;
  justify-content: space-evenly;
  gap: 3px;
  display: flex;
}

